html {
  position: relative;
  min-height: 100%;
}
body {
  margin-bottom: 60px;
}
a {
  color: #01a5c1;
}
.jumbotron {
  background-color: #222;
  color: #eee;
}
.jumbotron h1 {
  font-size: 3.7vw;
  color: #00d8ff;
}

.jumbotron p {
  font-size: 1.8vw;
  text-align: center;
}

#reactLogo {
  max-width: 12%;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 40px;
  background-color: #222;
  color: #00d8ff;
}
.footer a {
  color: #00d8ff;
  text-decoration: underline;
}
.footer-text {
  padding-top: 8px;
}



